import React, { useState } from 'react';

//data
import shopItems from '../data/data';

export default function Nav({ setDisplay }) {
    const [current, setCurrent] = useState(0);

    const filterItems = (e) => {
        const conditionState = e.target.innerHTML

        switch (conditionState) {
            case 'Todas':
                setDisplay(shopItems);
                setCurrent(0);
                break;
            case 'Sales':
                setDisplay(shopItems.filter(data => {
                    return data.sale === true;
                }));
                setCurrent(1);
                break;
            case "Best Seller":
                setDisplay(shopItems.filter(data => {
                    return data.bestseller >= 1;
                }).sort((a, b) => a.bestseller - b.bestseller));
                setCurrent(2);
                break;
            case "Para fiesta de baile":
                setDisplay(shopItems.filter(data => {return data.category === "Baile"}));
                setCurrent(3);
                break;
            case "Para fiesta de cumpleaños":
                setDisplay(shopItems.filter(data => {return data.category === "Cumpleanos"}));
                setCurrent(4);
                break;
            case "Para banquete":
                setDisplay(shopItems.filter(data => {return data.category === "Banquet"}));
                setCurrent(5);
                break;
            case "Para bautismo":
                setDisplay(shopItems.filter(data => {return data.category === "Bautismo"}));
                setCurrent(6);
                break;
            case "Para boda":
                setDisplay(shopItems.filter(data => {return data.category === "Boda"}));
                setCurrent(7);
                break;
        }
    }
    return(
        <nav>
            <h2>Categoría</h2>
            <ul>
                <li style={{textDecoration: current === 0 ? 'underline' : 'none'}} onClick={filterItems}>Todas</li>
                <li style={{textDecoration: current === 1 ? 'underline' : 'none'}} onClick={filterItems}>Sales</li>
                <li style={{textDecoration: current === 2 ? 'underline' : 'none'}} onClick={filterItems}>Best Seller</li>
            </ul>
            <h2>Departamentos</h2>
            <ul>
                <li style={{textDecoration: current === 3 ? 'underline' : 'none'}} onClick={filterItems}>Para fiesta de baile</li>
                <li style={{textDecoration: current === 4 ? 'underline' : 'none'}} onClick={filterItems}>Para fiesta de cumpleaños</li>
                <li style={{textDecoration: current === 5 ? 'underline' : 'none'}} onClick={filterItems}>Para banquete</li>
                <li style={{textDecoration: current === 6 ? 'underline' : 'none'}} onClick={filterItems}>Para bautismo</li>
                <li style={{textDecoration: current === 7 ? 'underline' : 'none'}} onClick={filterItems}>Para boda</li>
            </ul>
        </nav>
    )
}