import React from 'react';

export default function ItemCard({data}) {

    const renderBestSeller = () => {
        if (data.bestseller !== false) {
            return <h4>{data.bestseller === 1 ? "Numero Uno Best Seller":
                       data.bestseller === 2 ?  "Numero Dos Best Seller":
                       "Numero Tres Best Seller"}</h4>
        }
    }

    const renderSale = () => {
        if (data.sale === true) {
            return (
                <>
                <h4>Rebaja:</h4>
                <p>{data.salePrice} ({data.saleEsp})</p>
                </>
            )
        }
    }

    return (
        <div className="item-card">
            <h2>{data.item}</h2>
            <img src={data.img}></img>
            <h4>Precio</h4>
            <p>{data.cost} ({data.costEsp})</p>
            {renderSale()}
            {renderBestSeller()}
        </div>
    )
}